import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class TabPane extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('tab-pane',className);

		return (
			<div
				{...others}
				className={klassName}
				role="tabpane"
			>
				{children}
			</div>
		);
	}
};

TabPane.propTypes = {
	className: PropTypes.string,
	children: PropTypes.oneOfType([
		PropTypes.object,
		PropTypes.array
	])
};

export default TabPane;